.App {
  text-align: center;
}

#container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Roboto Mono";
}

#title {
  position: absolute;
  margin-top: 8%;
  z-index: 1;
  background-color: black;
  color: white;
  padding: 1rem;
  text-align: center;
}

.pixels {
  height: 8%;
  width: 8%;
  position: relative;
  border-radius: 1.3rem;
  z-index: 10;
}

#box {
  margin-top: 6vw;
  position: relative;
  height: 50vw;
  width: 50vw;
  border-width: 2vw;
  border-style: solid;
  border-color: black;
  z-index: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width:767px) {
	h1 {
		font-size: 16px;
}
}